
import { defineComponent, onMounted, ref, computed } from 'vue'
import PageSearch from '@/components/page-search'
import { useRouter } from 'vue-router'
import { searchFormConfig } from '../main/config/search.config'
import { useI18n } from 'vue-i18n'
import { PageInfo } from '@/views/main/customer/edit/types'
import PageContent from '@/components/page-content'
import {
  qualityStatusList,
  productState,
  isFragile,
  isDangerous,
  activeFlag
} from '@/constant/option'
import ImportDialog from '@/base-ui/importdialog'
import {
  ElMessage,
  ElMessageBox,
  ElCollapse,
  ElCollapseItem,
  ElTooltip
} from 'element-plus'

export default defineComponent({
  name: 'commodityentry-main',
  components: {
    PageSearch,
    PageContent,
    ImportDialog,
    ElCollapse,
    ElCollapseItem,
    ElTooltip
  },
  setup() {
    const { t } = useI18n()
    const activeNames = ref(['1'])
    const rowDataDetail = ref<any>({})
    const router = useRouter()
    let cellDialogVisible = ref(false)
    const importConfig = ref({
      tip: t('jpg/png files with a size less than 500kb'),
      href: '/static/customerTm.xlsx'
    })
    const keyWords = ref({})
    let importDialogVisible = ref(false)
    const handleQueryClick = (val: any) => {
      keyWords.value = val
      pageInfo.value.currentPage = 1
      searchInfo()
    }
    const handleSuccess = (file: any) => {
      console.log(file, '父filesuccess')
    }
    //
    const handleCell = (rowData: any) => {
      rowDataDetail.value = rowData
      cellDialogVisible.value = true
      console.log(rowData, '主')
    }
    const handleChange = (val: string[]) => {
      console.log(val)
    }
    const handleAddClick = (val: any) => {
      console.log(val, '2222valadd')
      router.push({ path: '/main/Inventory/commodityEntry/newCommodity' })
    }
    const productStateArray = computed(() => productState)
    const qualityStatusArray = computed(() => qualityStatusList)
    const isFragileArray = computed(() => isFragile)
    const isDangerousArray = computed(() => isDangerous)
    const activeFlagArray = computed(() => activeFlag)
    const handleRow = (rowData: any, label: any) => {
      if (label === 'Edit') {
        router.push({
          path: '/main/Inventory/commodityEntry/newCommodity',
          query: rowData
        })
      }
      if (label === 'Delete') {
        ElMessageBox.confirm(t('deleteChange'), t('deleteTips'), {
          distinguishCancelAndClose: true,
          confirmButtonText: t('confirm'),
          cancelButtonText: t('cancel'),
          customClass: 'message-box',
          center: true
        })
          .then(() => {
            ElMessage({
              type: 'success',
              message: t('deleteSuccessful')
            })
          })
          .catch((action: any) => {
            ElMessage({
              type: 'info',
              message: action === 'cancel' ? t('deleteFailed') : t('canceled')
            })
          })
      }
      console.log(rowData, label, 'row父')
    }
    const contentTableConfig = {
      propList: [
        {
          prop: 'barcode',
          label: t('barcode')
        },
        {
          prop: 'sku',
          label: t('sku'),
          minWidth: '100'
        },
        {
          prop: 'productName',
          label: t('productName'),
          minWidth: '100',
          handleCell: handleCell
        },
        {
          prop: 'productSpec',
          label: t('productSpec'),
          minWidth: '100'
        },
        {
          prop: 'productState',
          label: t('productState'),
          minWidth: '120',
          type: 'select',
          option: productStateArray
        },
        {
          prop: 'isFragile',
          label: t('isFragile'),
          minWidth: '100',
          type: 'select',
          option: isFragileArray
        },
        {
          prop: 'isDangerous',
          label: t('isDangerous'),
          minWidth: '120',
          type: 'select',
          option: isDangerousArray
        },
        {
          prop: 'activeFlag',
          label: t('activeFlag'),
          minWidth: '120',
          type: 'select',
          option: activeFlagArray
        }
      ],
      showIndexColumn: false,
      showSelectColumn: false,
      isOperation: true,
      operation: {
        label: t('operation'),
        width: '200',
        cols: [
          {
            label: t('edit'),
            type: 'primary',
            display: (data: any) => {
              return (
                data.activeFlag === 2 ||
                data.activeFlag === 1 ||
                data.activeFlag === 3
              )
            },
            handleRow: handleRow
          },
          {
            label: t('delete'),
            type: 'danger',
            handleRow: handleRow
          }
        ]
      },
      childrenProps: {
        rowKey: 'id',
        treeProp: {
          children: 'children'
        }
      },
      border: false,
      showFooter: true,
      totalCount: 12
    }
    const handleBtn = (label: any) => {
      if (label === t('import')) {
        importDialogVisible.value = true
        console.log(importDialogVisible.value, 'importDialogVisible1')
      }
      console.log(label, '1111')
    }
    const searchInfo = () => {
      const params = Object.assign({}, keyWords.value, {
        pageSize: pageInfo.value.pageSize,
        currentPage: pageInfo.value.currentPage
      })
      console.log(params, 'params查询')
    }
    const btnConfig = {
      isActions: true,
      addBtn: true,
      actions: [
        {
          label: t('import'),
          type: 'primary',
          style: 'plain',
          handleBtn: handleBtn
        },
        {
          label: t('export'),
          type: 'primary',
          style: 'plain',
          handleBtn: handleBtn
        }
      ]
    }
    const handleSizeChange = (val: number) => {
      pageInfo.value.pageSize = val
      searchInfo()
    }
    const handleCurrentChange = (val: number) => {
      pageInfo.value.currentPage = val
      searchInfo()
    }
    const beforeUpload = (file: any) => {
      if (file.type !== 'image/jpeg') {
        ElMessage.error('Avatar picture must be JPG format!')
        return false
      } else if (file.size / 1024 / 1024 > 2) {
        ElMessage.error('Avatar picture size can not exceed 2MB!')
        return false
      }
      return true
    }
    const pageInfo = ref<PageInfo>({
      totalCount: 100,
      pageSizes: [10, 20, 50, 100],
      handleSizeChange: handleSizeChange,
      handleCurrentChange: handleCurrentChange,
      currentPage: 1,
      pageSize: 20
    })
    const tableData = ref([
      {
        barcode: '02004(Vancouver PROD)',
        sku: '0232577453',
        productSpec: '5 packs',
        productState: '1',
        isFragile: '1',
        isDangerous: '2',
        activeFlag: 2,
        productName: 'Pallet 97'
      },
      {
        barcode: '02001(Vancouver PROD)',
        sku: '0232577451',
        productSpec: '9 packs',
        productState: '2',
        isFragile: '1',
        isDangerous: '2',
        activeFlag: 1,
        productName: 'Pallet 97'
      },
      {
        barcode: '02000(Vancouver PROD)',
        sku: '023257746',
        productSpec: '12 packs',
        productState: '2',
        isFragile: '1',
        isDangerous: '2',
        activeFlag: 3,
        productName: 'Pallet 97'
      }
    ])
    onMounted(() => {
      searchInfo()
    })

    return {
      searchFormConfig,
      handleQueryClick,
      contentTableConfig,
      pageInfo,
      tableData,
      handleCurrentChange,
      handleSizeChange,
      qualityStatusArray,
      btnConfig,
      ElMessage,
      importConfig,
      importDialogVisible,
      handleSuccess,
      beforeUpload,
      searchInfo,
      keyWords,
      handleAddClick,
      productStateArray,
      isFragileArray,
      isDangerousArray,
      activeFlagArray,
      activeNames,
      rowDataDetail,
      cellDialogVisible,
      handleChange,
      handleCell
    }
  }
})
